import Vue from 'vue'
import Router from 'vue-router'

// import app pages
import Home from '../components/Home'
import Admin from '../components/Admin'
import Login from '../components/Admin/Login'

// import admin page components
import Posts from '../components/Admin/content/post/Posts'
import PostNew from '../components/Admin/content/post/PostNew'
import PostEdit from '../components/Admin/content/post/PostEdit'

import Product from '../components/Admin/content/product/Product'
import ProductNew from '../components/Admin/content/product/ProductNew'
import ProductEdit from '../components/Admin/content/product/ProductEdit'

import Portofolio from '../components/Admin/content/portofolio/Portofolio'
import PortofolioNew from '../components/Admin/content/portofolio/PortofolioNew'
import PortofolioEdit from '../components/Admin/content/portofolio/PortofolioEdit'

import Settings from '../components/Admin/content/Settings'
import Pages from '../components/Admin/content/Pages'
import Media from '../components/Admin/content/Media'
import Database from '../components/Admin/content/Database'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [{
            path: '/',
            name: 'Login',
            component: Login
        },
        {
            path: '/admin',
            name: 'Admin',
            component: Admin,
            children: [{
                    path: 'posts',
                    component: Posts,
                    children: [{
                            path: 'new',
                            component: PostNew
                        },
                        {
                            path: 'edit/:key',
                            component: PostEdit
                        }
                    ]
                },
                {
                    path: 'product',
                    component: Product,
                    children: [{
                            path: 'new',
                            component: ProductNew
                        },
                        {
                            path: 'edit/:key',
                            component: ProductEdit
                        }
                    ]
                },
                {
                    path: 'portofolio',
                    component: Portofolio,
                    children: [{
                            path: 'new',
                            component: PortofolioNew
                        },
                        {
                            path: 'edit/:key',
                            component: PortofolioEdit
                        }
                    ]
                },
                {
                    path: 'settings',
                    component: Settings
                },
                {
                    path: 'pages',
                    component: Pages
                },
                {
                    path: 'media',
                    component: Media
                },
                {
                    path: 'database',
                    component: Database
                }
            ]
        },
        {
            path: '*',
            name: 'default',
            component: Home
        }
    ]
})


